.beers__card {
  background-color: white;
  border: 1px solid #9ca8bd;
  border-radius: 20px;
  padding: 20px 10px;
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
  align-items: center;
  align-content: center;
  gap: 10px;
  max-height: 380px;
  text-align: center;
  color: #222831;
  transition: 500ms ease-in-out;

  &:hover {
    transform: scale(1.05);
  }

  &__img {
    height: 200px;
    width: 150px;
    object-fit: contain;
  }

  &__heading {
    color: #f24033;
    font-size: 1.1rem;
  }

  &__tagline {
    color: #222831;
    font-style: italic;
    font-size: 0.9rem;
  }
}
